<template>
    <div class="settings-container">
        <transition name="fade">
            <div v-show="dataSaving" class="loading-overlay">
                <loader></loader>
            </div>
        </transition>
        <div class="wtw-container">
            <div class="back-to-main-menu" @click.click="goToMainMenu">
                <span class="stay-black">Back</span>
            </div>
            <div class="step-title-container">
                <div class="step-title">
                    Personal info
                </div>
            </div>
            <div class="wtw-form">
                <label>First name</label>
                <input
                    type="text"
                    placeholder="First Name"
                    v-model="user.profile_first_name"
                />
                <label>Last name</label>
                <input
                    type="text"
                    placeholder="Last Name"
                    v-model="user.profile_last_name"
                />
                <label>Email</label>
                <input
                    type="text"
                    placeholder="New Email"
                    v-model="user.user_email"
                />
            </div>

            <div class="step-title-container">
                <div class="step-title">
                    Update password
                </div>
            </div>
            <div class="wtw-form">
                <label>New password</label>
                <input
                    type="password"
                    placeholder="New password"
                    v-model="credentials.new_password"
                />
                <label>Confirm password</label>
                <input
                    type="password"
                    placeholder="Confirm password"
                    v-model="credentials.new_password_confirm"
                />
            </div>
            <div class="error-message error-relative" v-if="errorMessage">
                {{ errorMessage }}
            </div>
            <div class="center" v-if="!dataLoading">
                <div
                    @click.stop="updateUser"
                    class="button pink full-width small-font"
                >
                    Update
                </div>
            </div>
            <DeleteAccount />
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import Mixins from "../../../mixins/mixins";
import Loader from "../../common/loader.vue";
import DeleteAccount from "../delete-account/delete-account.vue";
import $ from "webpack-zepto";

export default {
    mixins: [Mixins],
    components: {
        Loader,
        DeleteAccount,
    },
    data() {
        return {
            sendOptions: false,
            errorMessage: null,
            dataSaving: false,
            dataLoading: false,
            email: null,
            credentials: {
                new_password: "",
                new_password_confirm: "",
            },
        };
    },
    computed: {
        ...mapGetters({
            styleSettingObject: "account/styleSetting",
            user: "auth/getUser",
        }),
    },
    methods: {
        updateUser() {
            this.dataSaving = true;
            this.$store
                .dispatch("auth/updateUser", {
                    user: this.user,
                    credentials: this.credentials,
                })
                .then((response) => {
                    this.errorMessage = null;
                    this.dataSaving = false;
                })
                .catch((response) => {
                    this.errorMessage = response;
                    this.dataSaving = false;
                });
        },
        goToMainMenu() {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "main-menu",
                    component: "main-menu/main-menu.vue",
                    isMenuExpanded: false,
                },
            });
        },
    },
};
</script>
